<script>
import { ref } from 'vue';
import { Init } from '@/models/wallet'
let Hls = require('hls.js');
export default {
    setup(){
        return {
            hls: '',
            screenWidth: document.body.clientWidth,
            currentComponent: null
        }
    },
    created: function() {
        
    },
    mounted (){
        this.getStream('https://cdn.jsdelivr.net/gh/tkbgame/website@main/media/playlist.m3u8')
    },
    methods:{
        connect(){
            Init((callback)=>{
                console.log(callback,4444444444444)
                if(callback.type === 'error'){
                    return false
                }
            })
        },
        getStream(source) {
            if (Hls.isSupported()) {
            this.hls = new Hls();
            this.hls.loadSource(source);
            this.hls.attachMedia(this.$refs.video);
            this.hls.on(Hls.Events.MANIFEST_PARSED, () => {
                console.log('加载成功');
                this.$refs.video.autoplay = true
                this.$refs.video.muted = true
                this.$refs.video.play();
            });
            this.hls.on(Hls.Events.ERROR, (event, data) => {
                // console.log(event, data);
                // 监听出错事件
                console.log('加载失败');
            });
            }
        },
        goPage (url) {
            window.open(url)
        }
    }
}
</script>

